@mixin img() {
  display: block;
}

@mixin list() {
  padding: 0;
  margin: 0;

  list-style: none;
}

@mixin link() {
  text-decoration: none;
}

@mixin visually-hidden() {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}